import { Injectable } from '@angular/core';

declare let window: any;
interface DBInterface {
    name: string;
    version: string;
    info: string,
    size: number;
    callback?: Function
}

@Injectable()
export class SqlService {
    private dataBase: any;
    private db: DBInterface = {
        name: 'SQL_DB',
        version: '1.0',
        info: 'LOVA DB',
        size: 2 * 1024 * 1024
    }

    constructor() {

    }

    private executeSql(sql: string, arr?: any[], callBack?: Function) {
        this.dataBase.transaction((tx) => {
            tx.executeSql(sql, arr, (tx, res) => {
                callBack&&callBack(tx, res);
            });
        });
    }

    /**
     *  数据库名称
     *   版本号
     *   描述文本
     *   数据库大小
     *   创建回调
     */

    public opendataBase(callBack?: Function): void {
        this.dataBase = window.openDatabase(this.db.name, this.db.version, this.db.info, this.db.size, callBack);
    }

    public createTable(sql: string) {
        this.executeSql(sql);
    }
    public deleteData(sql: string) {
        this.executeSql(sql);
    }
    public saveData(sql: string) {
        this.executeSql(sql);
    }
    public queryData(sql: string, res: Function) {
        this.executeSql(sql, [], res);
    }
    public updata(sql: string) {
        this.executeSql(sql);
    }

}


