import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';//双向绑定[(ngModel)]
import { RouterModule } from "@angular/router";
import { HttpModule, JsonpModule } from '@angular/http';
import { AppComponent } from './app.component';
import { WeatheComponent } from './weathe/weathe.component';
import { Route } from './routes';
import { AlertModule } from 'ng2-bootstrap';
import { EchartsNg2Module } from 'echarts-ng2';
import { EchartsComponent } from './echart/echart.component';
import { EchartsService } from './echart/echart.server';
import { FileComponent } from './file/file.component';
import { GetNamePipe } from './file/file.pipe';
import { OffComponent } from "./off/off.component";
import { CliComponent } from "./cli/cli.component";
import { BlogComponent } from "./blog/blog.component";
import { AqiRulerComponent } from "./aqi-ruler/aqiRuler.component";
import { setHeightPipe,WeatherIconPipe } from "./pipe/public.pipe";
import { SqlService } from './sql/sql.server';


@NgModule({
  declarations: [
    setHeightPipe,WeatherIconPipe,
    GetNamePipe, AppComponent, WeatheComponent, EchartsComponent, OffComponent, FileComponent, CliComponent, BlogComponent, AqiRulerComponent
  ],
  imports: [
    FormsModule,
    RouterModule, HttpModule, JsonpModule,
    RouterModule.forRoot(Route),
    BrowserModule,
    AlertModule.forRoot(), EchartsNg2Module
  ],
  providers: [EchartsService, SqlService],
  bootstrap: [AppComponent]
})
export class AppModule { }



