import { Component, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { EventManager } from '@angular/platform-browser';

declare let Buffer: any;
declare let document: any;

@Component({
    selector: 'cli',
    templateUrl: './cli.component.html',
    styleUrls: ['./cli.component.scss']
})


export class CliComponent implements OnDestroy {
    @ViewChild('showCMD') pre: ElementRef;
    @ViewChild('titleLabel') title: ElementRef;

    private autoCallBack: Function;
    private preHeight: number;


    constructor(private eventManager: EventManager) {
        this.bindResizeEvent();
        this.setHeight();
    }




    setHeight() {
        let height: number;

        height = document.querySelector('.view').offsetHeight - 100;
        if (height < 200) {
            height = 200;
        }

        this.preHeight = height;
    }

    bindResizeEvent() {
        this.autoCallBack = this.eventManager.addGlobalEventListener('window', 'resize', () => {

            this.setHeight();

        })
    }

    remove() {
        this.pre.nativeElement.textContent = '';
    }

    send(str: string) {

        let child_process = eval("require('child_process')"),
            iconv = eval("require('iconv-lite')"), encoding = 'cp936', binaryEncoding = 'binary';

        child_process.exec(str, { encoding: binaryEncoding }, (err, stdout, stderr) => {
            let info: string;
            let res: string;

            if (err) {
                res = err.message;
            } else {
                res = stdout;
            }

            info = iconv.decode(new Buffer(res, binaryEncoding), encoding), iconv.decode(new Buffer(res, binaryEncoding), encoding);

            this.pre.nativeElement.textContent = info;



        });

    }

    keyEvent(arg: any) {
        let value: string = arg.target.value;
        if (arg.key === 'Enter') {
            if (value === 'cls') {
                this.pre.nativeElement.textContent = '';
            } else {
                this.send(value);
            }
            arg.target.value = '';
        }



    }

    ngOnDestroy() {
        this.autoCallBack();
    }

}


