import { Component } from '@angular/core';


declare let Buffer: any;

@Component({
    selector: 'off',
    templateUrl: './off.component.html',
    styleUrls: ['./off.component.scss']
})


export class OffComponent {

    
    showip() {
        
        let child_process = eval("require('child_process')"),
            iconv = eval("require('iconv-lite')"), encoding = 'cp936', binaryEncoding = 'binary';

        child_process.exec('ipconfig -all', { encoding: binaryEncoding }, (err, stdout, stderr) => {
            let info:any;
        });


    }

}


