import { Component, Pipe, PipeTransform, Inject, forwardRef, Host } from '@angular/core';
import { EchartsComponent } from '../echart/echart.component';

@Component({
    selector: 'file-widget',
    templateUrl: './file.component.html',
    styleUrls: ['./file.component.scss']
})



export class FileComponent {



    dirs: string[] = [];

    fs: any = null;

    constructor( @Host() @Inject(forwardRef(() => EchartsComponent)) public echartsComponent: EchartsComponent) {

    }





    readDir(filePath) {
        var fs;
        try {
            fs = eval("require('fs')");
        } catch (e) { }
        if (!fs) {
            return;
        }


        //遍历文件夹，获取所有文件夹里面的文件信息
        /*
             * @param path 路径
             *
             */

        function geFileList(path) {
            var filesList = [];
            readFile(path, filesList);
            return filesList;
        }

        //遍历读取文件
        function readFile(path, filesList) {
            let files = fs.readdirSync(path);
            //需要用到同步读取
            files.forEach(walk);
            function walk(file) {
                let states = fs.statSync(path + '/' + file);
                if (states.isDirectory()) {
                    readFile(path + '/' + file, filesList);
                } else {
                    //创建一个对象保存信息
                    var obj: any = new Object();
                    obj.size = states.size;
                    //文件大小，以字节为单位
                    obj.name = file;
                    //文件名
                    obj.path = path + '/' + file;
                    //文件绝对路径
                    filesList.push(obj);
                }
            }
        }

        //写入文件utf-8格式
        function writeFile(fileName, data) {
            fs.writeFile(fileName, data, 'utf-8', complete);
            function complete() {
                console.log("文件生成成功");
            }
        }

        var filesList = geFileList(filePath);
        filesList.sort(sortHandler);
        function sortHandler(a, b) {
            if (a.size > b.size)
                return -1;
            else if (a.size < b.size)
                return 1
            return 0;
        }
        var echart={legend : [],series:[]};
        for (var i = 0; i < filesList.length; i++) {
            var item = filesList[i];
            echart.legend.push(item.name);
            echart.series.push({ name: item.name, value: Number((item.size / 1024).toFixed(2)), path: item.path });
        }

        //writeFile("test.txt", str);
        return echart;

    }
    
    bindResize(){
       // window.removeEventListener('resize:fileResizeEcharts');

    }


    openDir() {
        let echart:any,startTime,endTime;
        try {
            const { dialog } = eval("require('electron').remote");
            this.dirs = dialog.showOpenDialog({ properties: ['openFile', 'openDirectory', 'multiSelections'] });

        } catch (e) {
            this.dirs = [];
        }

        if (!this.dirs.length) {
            return;
        }

        // this.dirs.forEach((v, i) => {
        //     arr.push(this.readDir(v));
        // });
        startTime = new Date().getTime();
        echart = this.readDir(this.dirs[0]);
        endTime = new Date().getTime();

        let legend=[];    console.log(echart);

        


        this.echartsComponent.echartsService.chartData.title.subtext=`耗时：${endTime-startTime}ms`;
        this.echartsComponent.echartsService.chartData.legend.data= echart.legend;
        this.echartsComponent.echartsService.chartData.series['0'].data= echart.series;


       this.echartsComponent.echarts.setOption(this.echartsComponent.echartsService.chartData);
                
    }

}


