import { Component, Input, OnChanges, ViewChild, ElementRef, OnInit } from '@angular/core';


@Component({
    selector: 'aqi-ruler',
    templateUrl: './aqiRuler.component.html',
    styleUrls: ['./aqiRuler.component.scss']
})

export class AqiRulerComponent implements OnChanges, OnInit {
    @ViewChild('aqiLv') aqiLv: ElementRef;
    @ViewChild('scaleValue') scaleValue: ElementRef;
    @Input()
    content: number;
    private state: string;

    ngOnInit() {
        this.content = 0;
        this.setScale();
        this.setState();
        this.setClass();
    }

    constructor() {
        this.content = 0;
    }

    privateContent(value): boolean {

        if (!value && (value !== '0' || value !== 0)) {
            return true;
        }
        this.content = Number(this.content);
        if (!this.content) {
            return true;
        } else if (this.content < 0) {
            this.content = 0;
        } else if (this.content > 500) {
            this.content = 500;
        }
        return false;
    }

    ngOnChanges() {
        if (this.privateContent(this.content)) {
            return;
        }
        this.setScale();
        this.setState();
        this.setClass();
    }

    setScale() {
        this.scaleValue.nativeElement.style.left = this.content + 9 + 'px';
    }

    setState() {
        let state: string;
        let n: number = this.content;
        if (n <= 35) {
            state = '优';
        } else if (n > 35 && n <= 75) {
            state = '良';
        } else if (n > 75 && n <= 115) {
            state = '轻度污染';
        } else if (n > 115 && n <= 150) {
            state = '中度污染';
        } else if (n > 150 && n <= 250) {
            state = '重度污染';
        } else if (n > 250) {
            state = '严重污染';
        }
        this.state = state;
    }

    setClass() {
        let level: number = 1;
        let n: number = this.content;

        if (n <= 50) {
            level = 1;

        } else if (n > 50 && n <= 100) {
            level = 2;

        } else if (n > 100 && n <= 150) {
            level = 3;

        } else if (n > 150 && n <= 200) {
            level = 4;

        } else if (n > 200 && n <= 300) {
            level = 5;

        } else if (n > 300 && n <= 500) {
            level = 6;

        }

        this.aqiLv.nativeElement.setAttribute('color', 'lv' + level);

    }

}


