import { Component } from '@angular/core';
import { Jsonp, Http, Response } from '@angular/http';

declare let unescape: any;

interface Citycode {
    ID: string;
    cityName: string;
    cityEN: string;
    townID: string;
    townName: string;
    townEN: string;
}


@Component({
    selector: 'weathe',
    templateUrl: './weathe.component.html',
    styleUrls: ['./weathe.component.scss']
})

export class WeatheComponent {

    // private url = 'http://127.0.0.1:8888/queryWeather?callback=JSONP_CALLBACK&city=';
    private url = '/weather/forecast?city=';


    city: string = '南充';

    private cityList: Citycode[] | any;

    private weather;

    constructor(public jsonp: Jsonp, public http: Http) {

        http.get('data/citycode.json').map((res: Response) => {
            return res.json();
        }).subscribe(res => {
            this.cityList = res;
        });

        http.get('data/weatche-mock.json').map((res: Response) => {
            return res.json();
        }).subscribe(res => {
            this.city = res.city_name;
            this.weather = res;
        });

    }

    formData(str: string): string {
        return new Date(str).toLocaleString();
    }


    getCityID(name: string): Citycode | any {
        let res: Citycode;
        this.cityList.forEach((v, i) => {
            if (v.townName === name) {
                res = v;
                return false;
            }
        });
        return res;
    }

    query() {

        if (!this.city) {
            return;
        }

        let city = this.getCityID(this.city);

        if (city) {
            //this.jsonp.request(this.url + city.townID)
            this.http.get(this.url + city.townID).map((res: Response) => {
                return res.json();
            }).subscribe(res => {
                this.weather = res.weather['0'];
            })
        } else {
            alert('没找到城市ID');
        }




    }
}


