import { Component, ViewChild, Inject, OnDestroy } from '@angular/core';
import { EchartsService } from './echart.server';
import { EChartOption, ECharts } from 'echarts-ng2';
import { FileComponent } from '../file/file.component';
import { EventManager } from '@angular/platform-browser';

@Component({
    selector: 'help',
    templateUrl: './echart.component.html',
    styleUrls: ['./echart.component.scss'],
    viewProviders: [FileComponent]
})

export class EchartsComponent implements OnDestroy {

    echartData: EChartOption;
    manager: EventManager;
    windowResize;
    @ViewChild('fileEcharts') echarts: ECharts;

    constructor(public echartsService: EchartsService, public eventManager: EventManager) {
        this.initEchart();
    }


    initEchart() {
        this.echartData = this.echartsService.chartData;
        this.windowResize = this.eventManager.addGlobalEventListener('window', 'resize', () => {
            this.echarts.resize();
        })

    }

    ngOnDestroy() {
        this.windowResize();
    }



}


