import { Routes } from "@angular/router";
import { AppComponent } from "./app.component";
import { WeatheComponent } from "./weathe/weathe.component";
import { EchartsComponent } from "./echart/echart.component";
import { OffComponent } from "./off/off.component";
import { CliComponent } from "./cli/cli.component";
import { BlogComponent } from "./blog/blog.component";
const router: Routes = [
  // { path: '', component: EchartsComponent },
  {
    path: '',
    redirectTo: 'echart',
    pathMatch: 'full'
  },
  { path: 'off', component: OffComponent },
  { path: 'cli', component: CliComponent },
  { path: 'echart', component: EchartsComponent },
  { path: 'weathe', component: WeatheComponent },
  { path: 'blog', component: BlogComponent, loadChildren: './blog/blog.module#BlogModule' }
  //  ,{ path: '**', redirectTo: '', pathMatch: 'full' }

]

export const Route = router;


