import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
    name: 'setHeight'
})
export class setHeightPipe implements PipeTransform {

    transform(weather: any) {

        let old = 38;
        let value: number = 0;
        if (weather && weather.now) {

            value = Number(weather.now.feels_like);
            if (value < -50) {
                value = -50;
            } else if (value > 50) {
                value = 50;
            }
            value += 50;

        } else {
            value = 50;

        }
        return value * 0.76 + 'px';

    }
}
@Pipe({
    name: 'iconName'
})
export class WeatherIconPipe implements PipeTransform {
    transform(value: string) {
        let icon;
        let code = Number(value);
        switch (code) {
            // 晴
            case 0: icon = 'icon-tianqi-qing'; break;
            // 晴
            case 1: icon = 'icon-qing'; break;
            // 晴
            case 2: icon = 'icon-tianqiqingtian'; break;
            // 晴
            case 3: icon = 'icon-300tianqi_qing'; break;
            // 多云
            case 4: icon = 'icon-tianqiduoyun'; break;
            // 晴间多云
            case 5: icon = 'icon-tianqi51'; break;
            // 晴间多云
            case 6: icon = 'icon-tianqitubiao_qingtianbai'; break;
            // 大部多云
            case 7: icon = 'icon-tianqitubiao_duoyun'; break;
            // 大部多云
            case 8: icon = 'icon-duoyun'; break;

            // 阴
            case 9: icon = 'icon-kongqiyintian'; break;
            // 阵雨
            case 10: icon = 'icon-tianqizhenyu'; break;
            // 雷阵雨
            case 11: icon = 'icon-tianqileizhenyu'; break;
            // 雷阵雨伴有冰雹
            case 12: icon = 'icon-tianqitubiao_leizhenyudaibingbao'; break;
            // 小雨
            case 13: icon = 'icon-tianqitubiao_xiaoyu1'; break;
            // 中雨
            case 14: icon = 'icon-tianqitubiao_zhongyu1'; break;
            // 大雨
            case 15: icon = 'icon-dayu-tianqi'; break;
            // 暴雨
            case 16: icon = 'icon-tianqitubiao_baoyu1'; break;
            // 大暴雨
            case 17: icon = 'icon-309tianqi_dabaoyu'; break;
            // 特大暴雨
            case 18: icon = 'icon-310tianqi_tedabaoyu'; break;

            // 冻雨
            case 19: icon = 'icon-311tianqi_dongyu'; break;
            // 雨夹雪
            case 20: icon = 'icon-tianqi_yujiaxue'; break;
            // 阵雪
            case 21: icon = 'icon-tianqizhenxue'; break;
            // 小雪
            case 22: icon = 'icon-312tianqi_xiaoxue'; break;
            // 中雪
            case 23: icon = 'icon-tianqitubiao_zhongxue1'; break;
            // 大雪
            case 24: icon = 'icon-tianqitubiao_zhongzhuandaxue'; break;
            // 暴雪
            case 25: icon = 'icon-tianqitubiao_baoxue1'; break;

            // 浮尘
            case 26: icon = 'icon-tianqitubiao_fuchen'; break;
            // 扬沙
            case 27: icon = 'icon-tianqitubiao_yangsha1'; break;
            // 沙尘暴
            case 28: icon = 'icon-tianqi-shachenbao'; break;
            // 强沙尘暴
            case 29: icon = 'icon-316tianqi_shachen'; break;
            // 雾
            case 30: icon = 'icon-tianqiwu'; break;
            // 霾
            case 31: icon = 'icon-tianqitubiao_wumai'; break;
            // 风
            case 32: icon = 'icon-tianqitubiao_dafeng'; break;
            // 大风
            case 33: icon = 'icon-tianqi_dafeng'; break;
            // 飓风
            case 34: icon = 'icon-tianqitubiao_dafeng'; break;
            // 热带风暴
            case 35: icon = 'icon-tianqifengbao'; break;
            // 龙卷风
            case 36: icon = 'icon-tianqi-longjuanfeng'; break;

            // 冷
            case 37: icon = 'icon-tianqi-xue'; break;
            // 热
            case 38: icon = 'icon-meiqitianranqi'; break;
            // 未知
            case 99: icon = 'icon-tianqiyubao'; break;

            default: icon = 'icon-yichangtianqitixing-yunle'; break;
        }

        return icon;
    }
}


